html {
  overflow-y: scroll;
}

html, body {
  height: 100%;
}

.site-wrapper {
  min-height: 100%;
  /* equal to footer height */
  margin-bottom: -90px;
}

.site-wrapper:after {
  content: "";
  display: block;
}

.footer, .site-wrapper:after {
  /* .push must be the same height as footer */
  height: 90px;
}

.footer {
  background: #252331;
  border-top: 3px solid #6aa5cc;
  padding: 30px 0 0;
}

.footer .container {
  color: white;
}

.footer p {
  float: left;
}

.footer .footer-nav {
  float: right;
}

.footer .footer-nav li {
  display: inline-block;
  padding-left: 15px;
}

.footer .footer-nav li a {
  color: #00a8e2;
}

.footer .footer-nav li a:hover {
  color: #86E201;
  text-decoration: underline;
}

ul.sidebar-list li {
  padding: 4px 7px;
