.home_page .intro {
  background: url("../../images/bgHero.jpg") no-repeat 0 0 scroll transparent;
  background-size: cover;
  width: 100%;
  height: 70vh;
}

.home_page .intro .container {
  height: 100%;
  position: relative;
}

.home_page .intro .container #search-form {
  width: 57%;
  margin: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.home_page .intro .container #search-form h2 {
  color: white;
  font-size: 26px;
  padding-bottom: 20px;
  text-align: center;
}

.home_page .intro .container #search-form .input-text {
  height: 59px;
  border-radius: 3px;
  background: white;
  border: 1px solid #f2f1f2;
  padding: 0 20px;
  width: calc(100% - 190px);
  float: left;
  line-height: 59px;
  font-size: 21px;
  padding-top: 5px;
}

.home_page .intro .container #search-form .input-submit {
  border: 0;
  border-radius: 3px;
  height: 59px;
  line-height: 59px;
  font-size: 21px;
  display: inline-block;
  color: white;
  background: #00a8e2;
  width: 180px;
  text-align: center;
  float: left;
  margin-left: 10px;
  transition: all 0.2s ease;
  padding-top: 1px;
}

.home_page .intro .container #search-form .input-submit:hover {
  background: #0095c9;
}

.home_page .main-content {
