.heading-title {
  text-align: center;
  padding-top: 0;
  padding-bottom: 10px;
}

.heading-title h3 {
  color: #000e42;
  padding-bottom: 20px;
  display: inline-block;
  font-size: 28px;
  line-height: 31px;
  padding-left: 72px;
  padding-right: 72px;
  position: relative;
}

.heading-title h3:before {
  background: transparent url("../../images/heading-lines.svg") no-repeat scroll center center;
  content: "";
  display: block;
  height: 5px;
  left: 0;
  position: absolute;
  top: 12px;
  width: 56px;
}

.heading-title h3:after {
  background: transparent url("../../images/heading-lines.svg") no-repeat scroll center center;
  content: "";
  display: block;
